import React from 'react';
import { ListCategoryWrapper } from './style';
import { slugify, getDefaultVoted } from '../../../../services/functions';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { setBodyArticle, setSectionId, setCategoryId, setTitleArticle, setArticleId, setVoted } from '../../../../actions'

import * as Scroll from 'react-scroll';
import { scrollSpy } from 'react-scroll';

const Element = Scroll.Element;

class ListCategory extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            col: [[],[]]
        }

        this._handleColumnData = this._handleColumnData.bind(this);
    }

    componentDidMount() {
        this._handleColumnData(this.props);
        scrollSpy.update();
    }

    componentWillReceiveProps(nextProps){
        this._handleColumnData(nextProps)
    }

    _handleColumnData(props){
        const data = props.data.articles;
        let newState = [[],[]];


        if (data.length >= 5){
            newState[0] = data.slice(0, Math.round(data.length / 2));
            newState[1] = data.slice(newState[0].length, data.length);
        
          }else {
            newState[0] = data.slice(0, data.length);
          }

          this.setState({col: newState}, function () {
           
        })
    }


    render(){
        const { name } = this.props.data;
        const { col } = this.state;

        return (
            <Element name={name.match(/[a-zA-Z]+/g).join('-').toLowerCase()} id={name.match(/[a-zA-Z]+/g).join('-').toLowerCase()}>
            <ListCategoryWrapper>
                <h4 class="no-mg-top">{ name }</h4>
                <div className='col'>
                    {
                        this.state.col.map((value, index) => 
                        <div className='col--m6'>
                                <ul>
                                    { this.state.col[index].map(value => 
                                        <li><Link 
                                                onClick={() => {
                                                    this.props.setTitleArticle(value.name)
                                                    this.props.setBodyArticle(value.body)
                                                    this.props.setCategoryId(this.props.categoryId)
                                                    this.props.setSectionId(value.section_id)
                                                    this.props.setArticleId(value.id)
                                                    this.props.setVoted(getDefaultVoted(value.id))
                                                }}
                                                className='text-14' to={
                                                        {pathname:`/articles/${slugify(value.title,value.id)}`, 
                                                         }}>
                                            {value.name}
                                        </Link></li>)}
                                </ul>
                            </div>
                        )
                    }
                </div>
            </ListCategoryWrapper>
            </Element>
        )
    }
}

const mapStateToProps = (state) => {
    return { articles : state.article }
};
      
const mapDispatchToProps = (dispatch) => ({
    setBodyArticle: (payload) => dispatch(setBodyArticle(payload)),
    setSectionId: (payload) => dispatch(setSectionId(payload)),
    setCategoryId: (payload) => dispatch(setCategoryId(payload)),
    setTitleArticle: (payload) => dispatch(setTitleArticle(payload)),
    setArticleId: (payload) => dispatch(setArticleId(payload)),
    setVoted: (payload) => dispatch(setVoted(payload))
})
    
export default connect(mapStateToProps, mapDispatchToProps)(ListCategory);


