import styled from 'styled-components';
import { sc_media } from '../../../utils';

export const ListCategoryWrapper = styled.div `
    padding-top: 1em;
    h4 {
        margin-bottom: 23px;
        margin-top: 2em;
    }

    &:first-child {
        h4 {
            margin-top: 1em;
        }
    }
    
    ul {
        list-style: none;
        padding-left: 0;
        margin: 0;
    
        li {
            margin: 19px 0;
    
            &:first-child {
                margin-top: 0;
            }
            &:last-child {
                margin-bottom: 0;
            }
        }

        ${sc_media.phone `
            margin: 16px 0;
        `}

    }

`


