import styled from 'styled-components';
import { media } from '../../../utils/responsive-style';
import { ButtonGhost } from '../../../commons/Buttons';
import { sc_color } from '../../../utils/index';

const DesktopNavigationWrapper = styled.div`
    border-bottom: 1px solid rgba(0,0,0,0.1);

    .container {
        display: flex;
        align-items: center;

        .input-search {
            width: 22%;
            input {
                flex-basis: 22%;
            }
        }
    }


    ${media.phone `
        background-color: white;
        padding: 1.5em 0;
        width: 100%;
        box-shadow: 0 2px 3px 0 rgba(175,175,175,0.5);
        border-bottom: 0;

        .container {
            .input-search {
                display: none;
            }
        }
    `}
`

const NavigationListContainer = styled.nav `
    width: 78%;

    ${media.phone `
        display: flex;
        width: 100%;
        `
    }

`

const ButtonNav = ButtonGhost.extend `
    margin: 1em .4em 1em 0;
    padding: .8em 2em;

    &.active {
        background-color: ${sc_color.brand};
    }

    ${media.phone `
        margin: 0;
        border: 1px solid rgba(0,0,0,0.1);
        font-size: 14px;
        padding: 0.5em 1.3em;
        display: flex;
        width: calc((100% / 3));
        justify-content: center;
        padding: .9em;

        &:first-of-type {
            span {
                &:first-child {
                    display: none;
                }

                &:last-child {
                    display: block;
                }
            }
        }

        &:nth-child(even) {
            margin-left: 1.5%;
            margin-right: 1.5%;
            @media screen and (max-width:340px) {
                margin-left: 1.8%;
                margin-right: 1.8%;
            }
        }
    `};
}
`






export { DesktopNavigationWrapper, NavigationListContainer, ButtonNav };


