import React from 'react';
import { WrapperNotFound } from './style';

import srcImg from '../../assets/help-1.svg';
import { Link } from 'react-router-dom';

const NotFound = (props) => (
    <WrapperNotFound className={props.className}>
        <img src={srcImg} alt='not found' />
        <h4>404 - Page Not Found</h4>
        <p>This page was lost, <Link to={'/'}>browse knowladge base</Link></p>
    </WrapperNotFound>
)

export default NotFound;


