import React from 'react';
import ReactDOM from 'react-dom';
// import { Link } from 'react-router-dom';
import * as Scroll from 'react-scroll';
import { Link, animateScroll as scroll, scrollSpy, scroller, animateScroll } from 'react-scroll';

import { SideNav } from './style';

const Events = Scroll.Events;

class ContentSideNav extends React.Component {
    constructor() {
        super();
        this.state = {
            activeSideNav: false,
            offsetSideNav: 0
        }

        this._handleScroll = this._handleScroll.bind(this);
    }
    componentDidMount() {
        Events.scrollEvent.register('begin', function (to, element) {
            console.log("begin", arguments);
        });

        Events.scrollEvent.register('end', function (to, element) {
            console.log("end", arguments);
        });

        scrollSpy.update();
        this.setState({
            offsetSideNav: Math.abs(this.props.offsetTop - (this.sideNavList.getBoundingClientRect().top + document.documentElement.scrollTop))
        })
        // console.log('client top', this.props.offsetTop, this.sideNavList.getBoundingClientRect().top + document.documentElement.scrollTop)
        window.addEventListener('scroll', this._handleScroll)
    }

    componentWillMount() {
        Events.scrollEvent.remove('begin');
        Events.scrollEvent.remove('end');
    }

    _handleScroll() {
        const scrollTop = (window.pageYOffset !== undefined) ? window.pageYOffset : (document.documentElement || document.body.parentNode || document.body).scrollTop;
        // console.log('propsofsetTop', this.props.offsetTop)
        if (scrollTop >= this.props.offsetTop) {
            this.setState({ activeSideNav: true })
        } else {
            this.setState({ activeSideNav: false })
        };
    }

    handleSetActive(to) {
        console.log('to', to)
    }

    handleSetInactive(to, element) {
        to == 'verifikasi' ? console.log('oke inactive') : console.log('nope');
        console.log('props 0', this.props.datanav);
        console.log('inactive to', to, element)
    }

    scrollTo(to){
        scroller.scrollTo(to, {
            offset: -70,
            smooth: true,
            duration: 200
        })
    }

    render() {
        const sideNavListStyle = {
            top: this.state.offsetSideNav
        }
        return (
            <SideNav className="">
                <ul className={`list-nostyle bg-white ${this.state.activeSideNav ? 'active' : ''}`} ref={ul => this.sideNavList = ul} style={sideNavListStyle}>
                    {
                        this.props.datanav.map((item, i) => <li key={i}><Link
                        to={item.match(/[a-zA-Z]+/g).join('-').toLowerCase()}
                        spy={true}
                        onClick={() => this.scrollTo(item.match(/[a-zA-Z]+/g).join('-').toLowerCase())}
                        >{item.match(/[a-zA-Z]+/g).join(' ')}</Link></li>)
                    }
                </ul>
            </SideNav>
        );
    }
}

export default ContentSideNav;


