import React from 'react';
import { BreadcrumbList } from './style'
import { Link } from 'react-router-dom';

class Breadcrumb extends React.Component {
    render() {
        return (
            <BreadcrumbList>
                <li>
                    <Link to="/">
                        Help Center
                    </Link>
                </li>
                {
                    this.props.crumb.map( (item, i) => <li key={i}>
                        {
                            i === this.props.crumb.length-1 ?
                            <span>{ item.name }</span> :
                            <Link to={item.url}>{ item.name }</Link>
                        }
                        </li>
                    )
                }
            </BreadcrumbList>
        )
    }
}

export default Breadcrumb;


