import React from 'react';
import ReactDOM from 'react-dom';
import { withRouter } from 'react-router';
import { connect } from 'react-redux';

// Component Import 
import DesktopNavigation from '../../components/layouts/Header/DesktopSecondaryNav';
import SectionHomeFourth from '../../components/commons/Sections/Section-Home-4';
import ListCategory from '../../components/commons/Lists/ListCategory';
import Breadcrumb from '../../components/commons/Breadcumbs';
import ContentSideNav from '../../components/commons/ContentSideNav'
import NotFound from '../NotFound';
import SEO from '../../components/layouts/SEO';

// Utilities Import 
import { getAllArticles, getAllSection, checkOnline } from '../../services/api';
import { loadAllData, slugify } from '../../services/functions';
import { SingleSkeleton, ParagraphSkeleton } from '../../components/commons/SkeletonBar';
import { setAllArticle, setAllSection, setCategoryId } from '../../actions';
import { defaultCategories } from '../../constants/globalVariable';
import { findIndex, propEq } from 'ramda';

import * as Scroll from 'react-scroll';
import { scrollSpy } from 'react-scroll';

const Events = Scroll.Events;

class CategoryPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            articles: [],
            choiceCategory: null,
            loading: true,
            offsetContent : null,
        }
        this._sortingCategory = this._sortingCategory.bind(this);
        this._renderList = this._renderList.bind(this);
        this._renderSideNav = this._renderSideNav.bind(this);
        this._handlerOffline = this._handlerOffline.bind(this);
        this._handlerOnline = this._handlerOnline.bind(this);
    }

    async _handlerOnline(online){
        if (!this.props.articles.get('articles').size)
            await loadAllData(getAllArticles,'articles')( (data) => {
                localStorage.setItem('articles', JSON.stringify(data))
                this.props.setAllArticle(data)});
        
        if (!this.props.articles.get('sections').size)
            await loadAllData(getAllSection,'sections')(data => {
                localStorage.setItem('sections', JSON.stringify(data))
                this.props.setAllSection(data)
        });

    }

    async _handlerOffline(){
        this.props.setAllArticle(JSON.parse(localStorage.getItem('articles')))
        this.props.setAllSection(JSON.parse(localStorage.getItem('sections')))
    }

    async componentDidMount() {

        Events.scrollEvent.register('begin', function (to, element) {
            console.log("begin", arguments);
        });

        Events.scrollEvent.register('end', function (to, element) {
            console.log("end", arguments);
        });

        scrollSpy.update();
        await checkOnline({
            resolve: this._handlerOnline,
            reject: this._handlerOffline
        })


        await this.setState({choiceCategory: this.props.match.params.id});
        
        this._sortingCategory();

        await this.props.setCategoryId(this.props.match.params.id);

        const offsetContent = this.categorySection.getBoundingClientRect().top + document.documentElement.scrollTop - 70;
        this.setState({offsetContent});
    }

    async _sortingCategory(){

       let { articles, sections } =  this.props.articles.toJS();

       let newSection = Object.values(sections);

       let sortedArticle = defaultCategories.reduce((acc, curr, i) => {
           acc[curr.id] = curr;
           acc[curr.id].sections = [];
           return acc;
       },{});


       // reset value of article
      await newSection.map((value, index) => newSection[index].articles = [])

       // Sorted Articles by Section ID
      await Object.values(articles).map(prop => {
           let indexSection = findIndex(propEq('id', prop.section_id), newSection);
           if (!newSection[indexSection].articles) 
                newSection[indexSection].articles = [];
                
           newSection[indexSection].articles.push(prop);
     })
       
       //Sorted Sections by Category ID
       newSection.map(value => sortedArticle[value.category_id].sections.push(value));

       this.setState({articles: sortedArticle, loading: false});
    }
    
    async componentWillReceiveProps(nextProps){
        await this.props.setCategoryId(nextProps.match.params.id)
    }

    _renderList(categoryId){
        // console.log('props match id', this.props.match.params.id);
        if (this.state.articles[this.props.match.params.id]){
            return Object.keys(this.state.articles[this.props.match.params.id].sections).map((key, index) => 
            <ListCategory data={this.state.articles[this.props.match.params.id].sections[index]}
            categoryId={categoryId} key={index}/>)
        }else {
            return <NotFound />
        }
    }

    _renderSideNav(categoryId){
        if(this.state.articles[this.props.match.params.id]){
            const matchingCategory =  Object.values(this.props.articles.toJS().sections)
                    .filter((item, index) => this.props.articles.toJS().sections[index].category_id === +this.props.articles.toJS().categoryId)
                    .map((item, index) => item.name);
            return <ContentSideNav datanav={matchingCategory} offsetTop={this.state.offsetContent}/>
        }
    }   

    _getCategory(prop){
        try {
            return defaultCategories.find(value => value.id == this.props.articles.toJS().categoryId)[prop]
        }catch(e) {
            return ''
        }
    }

    render(){
        return (
            <div>
                {
                    this.props.articles.toJS().categoryId &&
                    <SEO
                        schema={'http://127.0.0.1:3000/categories'}
                        title={`Kategori ${ this._getCategory('name') || 'di'} - Kitabisa Help Center`}
                        description={this._getCategory('description') || 'Kitabisa kategori'}
                        path={`http://127.0.0.1:3000${this.props.location.pathname}`}
                        contentType={`text/html; charset=utf-8`}
                        category={ this._getCategory('name') || 'Kategori'}
                        tags={`List Kategori`}
                    />
                }
                 <main className='js-header-sticky'>
                    <DesktopNavigation categoryId={this.props.articles.toJS().categoryId}/>
                    <section className='no-border' ref={(section) => { this.categorySection = section; }}>
                        <div className='container'>
                            {
                                this.state.articles[this.props.match.params.id] &&!this.state.loading ?
                                <Breadcrumb crumb={
                                    [{
                                        name: this._getCategory('name') || '',
                                        url: this.props.location.pathname
                                    }]
                                }/> :
                                null
                            }
                            <SectionHomeFourth>
                                {
                                    !this.state.loading ?
                                    <div>
                                    {this._renderList(this.props.articles.toJS().categoryId)}
                                    </div> :          
                                    <div className='lg-pd-top'>
                                        <SingleSkeleton width={'230px'} className='lg-mg-bottom' darken/>
                                        <ParagraphSkeleton />
                                    </div>
                                }
                            </SectionHomeFourth>
                            {
                                !this.state.loading &&
                                this._renderSideNav(this.props.articles.toJS().categoryId)
                            }
                        </div>
                    </section>
                </main>
            </div>
        )
    }

}

const mapStateToProps = (state) => {
    return { articles: state.article }
};

const mapDispatchToProps = (dispatch) => ({
    setAllArticle: (payload) => dispatch(setAllArticle(payload)),
    setAllSection: (payload) => dispatch(setAllSection(payload)),
    setCategoryId: (payload) => dispatch(setCategoryId(payload))
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CategoryPage));


