import styled from 'styled-components';
import { sc_color } from '../../utils';

export const SideNav = styled.div`
    // display: inline-flex;
    // width: 30%;
    vertical-align: top;
    display: table-cell;
    width: 30%;
    // padding: 10px 10px 0 10px;
    ul{
        min-height: 245px;
        padding: 1em 2em;
        width: 288px;
        li{
            padding: .5em 0;
            cursor: pointer;
            font-size: 14px;

            a{
                color: ${sc_color.defaultText};
                &.active{
                    color: ${sc_color.brand};
                }
            }
        }
        
        &.active{
            position: fixed;
        }
    }

    @media screen and (max-width: 767px) {
        display: none;
        visibility: hidden;
    }

`


