import styled from 'styled-components';
import { sc_space } from '../../../utils';

const colorButton = {
    blue : '#3198E8'
}

const colorHoverButton = {
    blue: '#1E8CE1'
}
const Button = styled.button `
    display: inline-block;
    padding: 11px 30px;
    border: 0px;
    font-weight: 600;
    line-height: normal;
    text-align: center;
    vertical-align: middle;
    color: #333333;
    background: #ddd;
    cursor: pointer;
    user-select: none;
    border-radius: 4px;
    width: ${props => props.block ? '100%' : 'auto'};
    font-family: "Open Sans", sans-serif;
    -webkit-tap-highlight-color: rgba(0,0,0,0);

    &:focus {
        outline: none;
    }

    &[disabled] {
        cursor: not-allowed;
    }

    .img-icon {
        max-height: 18px;
        vertical-align: top;
        display: inline-block;
        margin: 0 5px;
    }

    .btn-text, .btn-icon {
        display: inline-block;
        vertical-align: middle;
    }

    .fa {
        font-size: 18px;
        margin-right: 7px;
    }

    a {
        color: #414141;
    }

`

const ButtonBlue = Button.extend `
    background-color: #3198E8;
    color: white;

    &:hover {
        background-color: #1E8CE1;
        box-shadow: inset 0 -2px 0 rgba(31,45,61,.15);
    }

    &:focus {
        box-shadow: inset 0 2px 0 rgba(31,45,61,.15);
    }
`


export { Button, ButtonBlue }






