import React from 'react';
import { DesktopNavigationWrapper, NavigationListContainer, ButtonNav } from './style';
import { defaultCategories } from '../../../../constants/globalVariable';
import { setCategoryId } from '../../../../actions';
import { withRouter } from 'react-router-dom';
import queryString from 'query-string';
import InputSearch from '../../../commons/Inputs/InputSearch';
import { compose, withState, withHandlers } from 'recompose';
import { connect } from 'react-redux';



const enchance = compose(
    withState('value', 'updateValue', ''),
    withHandlers({
        onChange: props => event => props.updateValue(event.target.value),
        handleSubmit: props => value => {
            console.log('tada', value);
            props.history.push(`/articles/search?${queryString.stringify({q: value})}`)
        },
        resetValue: props => () =>  props.updateValue(''), 
      })
);

const DesktopSecond = enchance(({ value, history, categoryId, test, onChange, resetValue, handleSubmit, setCategoryId}) => (
        <DesktopNavigationWrapper>
            <div className='container'>
                <NavigationListContainer>
                    {
                        defaultCategories.map(value => 
                        <ButtonNav onClick={() => {
                            history.push(`/categories/${value.id}`)
                            setCategoryId(value.id)
                        }} 
                                   backgroundColor={'#e7e9eb'}
                                   fontColor={'#414141'}
                                   className={categoryId == value.id && 'active'}>
                            { value.name }
                       </ButtonNav>
                        )
                    }
                </NavigationListContainer>
                <InputSearch
                        className={'input-search'}
                        type={'search'} 
                        placeholder={'Cari ...'}
                        value={value}
                        onChange={onChange}
                        resetValue={resetValue}
                        handleSubmit={handleSubmit}
                        bordered={true}/>
            </div>
        </DesktopNavigationWrapper>
    ))

const mapStateToProps = (state) => {
    return { articles : state.article }
};
  
const mapDispatchToProps = (dispatch) => ({
    setCategoryId: (payload) => dispatch(setCategoryId(payload)),
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(DesktopSecond));


