import styled from 'styled-components';
import { sc_media } from '../../components/utils'

export const WrapperNotFound = styled.div `
    text-align: center;
    width: 136px;
    margin-left: -1em;
    width: 100%;
    padding-top: 4em;

    h4 {
        font-size: 18px;
        margin: 8px;
        margin-top: 31px;
    }

    p {
        font-size: 14px;
        margin: 0;
    }

    ${sc_media.phone `
        margin-left: 0;
    `}
`


