import styled from 'styled-components';
import { sc_color } from '../../utils';

export const BreadcrumbList = styled.ul `
    list-style: none;
    margin: 0 0 17px 0;
    padding: 0 10px;

    li{
        display: inline;
        color: ${sc_color.brand};
        font-size: 12px;

        &:after{
            content: '>';
            margin: 0 .5em;
            color: ${sc_color.defaultText};
        }

        &:last-child{
            color: ${sc_color.defaultText};

            &:after{
                content: '';
            }
        }
    }
`


